&:before {\r\n    content: '\\E5CC';\r\n    color: rgba(255,255,255, .7);\r\n    vertical-align: top;\r\n    display: inline-block;\r\n    font-family: 'Material Icons';\r\n    font-weight: normal;\r\n    font-style: normal;\r\n    font-size: 25px;\r\n    margin: 0 10px 0 8px;\r\n    -webkit-font-smoothing: antialiased;\r\n  }\r\n\r\n  &:first-child:before {\r\n    display: none;\r\n  }\r\n\r\n  &:last-child {\r\n    color: #fff;\r\n  }\r\n}\r\n\r\n// Parallax\r\n.parallax-container {\r\n  position: relative;\r\n  overflow: hidden;\r\n  height: 500px;\r\n\r\n  .parallax {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    z-index: -1;\r\n\r\n    img {\r\n      display: none;\r\n      position: absolute;\r\n      left: 50%;\r\n      bottom: 0;\r\n      min-width: 100%;\r\n      min-height: 100%;\r\n      transform: translate3d(0,0,0);\r\n      transform: translateX(-50%);\r\n    }\r\n  }\r\n}\r\n\r\n// Pushpin\r\n.pin-top, .pin-bottom {\r\n  position: relative;\r\n}\r\n.pinned {\r\n  position: fixed !important;\r\n}\r\n\r\n/*********************\r\n  Transition Classes\r\n**********************/\r\n\r\nul.staggered-list li {\r\n  opacity: 0;\r\n}\r\n\r\n.fade-in {\r\n  opacity: 0;\r\n  transform-origin: 0 50%;\r\n}\r\n\r\n\r\n/*********************\r\n  Media Query Classes\r\n**********************/\r\n.hide-on-small-only, .hide-on-small-and-down {\r\n  @media #{$small-and-down} {\r\n    display: none !important;\r\n  }\r\n}\r\n.hide-on-med-and-down {\r\n  @media #{$medium-and-down} {\r\n    display: none !important;\r\n  }\r\n}\r\n.hide-on-med-and-up {\r\n  @media #{$medium-and-up} {\r\n    display: none !important;\r\n  }\r\n}\r\n.hide-on-med-only {\r\n  @media only screen and (min-width: $small-screen) and (max-width: $medium-screen) {\r\n    display: none !important;\r\n  }\r\n}\r\n.hide-on-large-only {\r\n  @media #{$large-and-up} {\r\n    display: none !important;\r\n  }\r\n}\r\n.show-on-large {\r\n  @media #{$large-and-up} {\r\n    display: block !important;\r\n  }\r\n}\r\n.show-on-medium {\r\n  @media only screen and (min-width: $small-screen) and (max-width: $medium-screen) {\r\n    display: block !important;\r\n  }\r\n}\r\n.show-on-small {\r\n  @media #{$small-and-down} {\r\n    display: block !important;\r\n  }\r\n}\r\n.show-on-medium-and-up {\r\n  @media #{$medium-and-up} {\r\n    display: block !important;\r\n  }\r\n}\r\n.show-on-medium-and-down {\r\n  @media #{$medium-and-down} {\r\n    display: block !important;\r\n  }\r\n}\r\n\r\n\r\n// Center text on mobile\r\n.center-on-small-only {\r\n  @media #{$small-and-down} {\r\n    text-align: center;\r\n  }\r\n}\r\n\r\n// Footer\r\n.page-footer {\r\n  padding-top: 20px;\r\n  color: $footer-font-color;\r\n  background-color: $footer-bg-color;\r\n\r\n  .footer-copyright {\r\n    overflow: hidden;\r\n    min-height: 50px;\r\n    display: flex;\r\n    align-items: center;\r\n    padding: 10px 0px;\r\n    color: $footer-copyright-font-color;\r\n    background-color: $footer-copyright-bg-color;\r\n    @extend .light;\r\n  }\r\n}\r\n\r\n// Tables\r\ntable, th, td {\r\n   border: none;\r\n}\r\n\r\ntable {\r\n  width:100%;\r\n  display: table;\r\n\r\n  &.bordered > thead > tr,\r\n  &.bordered > tbody > tr {\r\n    border-bottom: 1px solid $table-border-color;\r\n  }\r\n\r\n  &.striped > tbody {\r\n    > tr:nth-child(odd) {\r\n      background-color: $table-striped-color;\r\n    }\r\n\r\n    > tr > td {\r\n      border-radius: 0;\r\n    }\r\n  }\r\n\r\n  &.highlight > tbody > tr {\r\n    transition: background-color .25s ease;\r\n    &:hover {\r\n      background-color: $table-striped-color;\r\n    }\r\n  }\r\n\r\n  &.centered {\r\n    thead tr th, tbody tr td {\r\n      text-align: center;\r\n    }\r\n  }\r\n\r\n}\r\n\r\nthead {\r\n  border-bottom: 1px solid $table-border-color;\r\n}\r\n\r\ntd, th{\r\n  padding: 15px 5px;\r\n  display: table-cell;\r\n  text-align: left;\r\n  vertical-align: middle;\r\n  border-radius: 2px;\r\n}\r\n\r\n// Responsive Table\r\n@media #{$medium-and-down} {\r\n\r\n  table.responsive-table {\r\n    width: 100%;\r\n    border-collapse: collapse;\r\n    border-spacing: 0;\r\n    display: block;\r\n    position: relative;\r\n\r\n    td:empty:before {\r\n      content: '\\00a0';\r\n    }\r\n\r\n    th,\r\n    td {\r\n      margin: 0;\r\n      vertical-align: top;\r\n    }\r\n\r\n    th { text-align: left; }\r\n    thead {\r\n      display: block;\r\n      float: left;\r\n\r\n      tr {\r\n        display: block;\r\n        padding: 0 10px 0 0;\r\n\r\n        th::before {\r\n          content: \"\\00a0\";\r\n        }\r\n      }\r\n    }\r\n    tbody {\r\n      display: block;\r\n      width: auto;\r\n      position: relative;\r\n      overflow-x: auto;\r\n      white-space: nowrap;\r\n\r\n      tr {\r\n        display: inline-block;\r\n        vertical-align: top;\r\n      }\r\n    }\r\n    th {\r\n      display: block;\r\n      text-align: right;\r\n    }\r\n    td {\r\n      display: block;\r\n      min-height: 1.25em;\r\n      text-align: left;\r\n    }\r\n    tr { padding: 0 10px; }\r\n\r\n    /* sort out borders */\r\n    thead {\r\n      border: 0;\r\n      border-right: 1px solid $table-border-color;\r\n    }\r\n\r\n    &.bordered {\r\n      th { border-bottom: 0; border-left: 0; }\r\n      td { border-left: 0; border-right: 0; border-bottom: 0; }\r\n      tr { border: 0; }\r\n      tbody tr { border-right: 1px solid $table-border-color; }\r\n    }\r\n\r\n  }\r\n\r\n}\r\n\r\n\r\n// Collections\r\n.collection {\r\n  margin: $element-top-margin 0 $element-bottom-margin 0;\r\n  border: 1px solid $collection-border-color;\r\n  border-radius: 2px;\r\n  overflow: hidden;\r\n  position: relative;\r\n\r\n  .collection-item {\r\n    background-color: $collection-bg-color;\r\n    line-height: $collection-line-height;\r\n    padding: 10px 20px;\r\n    margin: 0;\r\n    border-bottom: 1px solid $collection-border-color;\r\n\r\n    // Avatar Collection\r\n    &.avatar {\r\n      min-height: 84px;\r\n      padding-left: 72px;\r\n      position: relative;\r\n\r\n      // Don't style circles inside preloader classes.\r\n      &:not(.circle-clipper) > .circle,\r\n      :not(.circle-clipper) > .circle {\r\n        position: absolute;\r\n        width: 42px;\r\n        height: 42px;\r\n        overflow: hidden;\r\n        left: 15px;\r\n        display: inline-block;\r\n        vertical-align: middle;\r\n      }\r\n      i.circle {\r\n        font-size: 18px;\r\n        line-height: 42px;\r\n        color: #fff;\r\n        background-color: #999;\r\n        text-align: center;\r\n      }\r\n\r\n\r\n      .title {\r\n        font-size: 16px;\r\n      }\r\n\r\n      p {\r\n        margin: 0;\r\n      }\r\n\r\n      .secondary-content {\r\n        position: absolute;\r\n        top: 16px;\r\n        right: 16px;\r\n      }\r\n\r\n    }\r\n\r\n\r\n    &:last-child {\r\n      border-bottom: none;\r\n    }\r\n\r\n    &.active {\r\n      background-color: $collection-active-bg-color;\r\n      color: $collection-active-color;\r\n\r\n      .secondary-content {\r\n        color: #fff;\r\n      }\r\n    }\r\n  }\r\n  a.collection-item{\r\n    display: block;\r\n    transition: .25s;\r\n    color: $collection-link-color;\r\n    &:not(.active) {\r\n      &:hover {\r\n        background-color: $collection-hover-bg-color;\r\n      }\r\n    }\r\n  }\r\n\r\n  &.with-header {\r\n    .collection-header {\r\n      background-color: $collection-bg-color;\r\n      border-bottom: 1px solid $collection-border-color;\r\n      padding: 10px 20px;\r\n    }\r\n    .collection-item {\r\n      padding-left: 30px;\r\n    }\r\n    .collection-item.avatar {\r\n      padding-left: 72px;\r\n    }\r\n  }\r\n\r\n}\r\n// Made less specific to allow easier overriding\r\n.secondary-content {\r\n  float: right;\r\n  color: $secondary-color;\r\n}\r\n.collapsible .collection {\r\n  margin: 0;\r\n  border: none;\r\n}\r\n\r\n\r\n\r\n// Responsive Videos\r\n.video-container {\r\n    position: relative;\r\n    padding-bottom: 56.25%;\r\n    height: 0;\r\n    overflow: hidden;\r\n\r\n    iframe, object, embed {\r\n      position: absolute;\r\n      top: 0;\r\n      left: 0;\r\n      width: 100%;\r\n      height: 100%;\r\n    }\r\n}\r\n\r\n// Progress Bar\r\n.progress {\r\n    position: relative;\r\n    height: 4px;\r\n    display: block;\r\n    width: 100%;\r\n    background-color: lighten($progress-bar-color, 40%);\r\n    border-radius: 2px;\r\n    margin: $element-top-margin 0 $element-bottom-margin 0;\r\n    overflow: hidden;\r\n  .determinate {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    bottom: 0;\r\n    background-color: $progress-bar-color;\r\n    transition: width .3s linear;\r\n  }\r\n  .indeterminate {\r\n    background-color: $progress-bar-color;\r\n    &:before {\r\n      content: '';\r\n      position: absolute;\r\n      background-color: inherit;\r\n      top: 0;\r\n      left:0;\r\n      bottom: 0;\r\n      will-change: left, right;\r\n      // Custom bezier\r\n      animation: indeterminate 2.1s cubic-bezier(0.650, 0.815, 0.735, 0.395) infinite;\r\n\r\n    }\r\n    &:after {\r\n      content: '';\r\n      position: absolute;\r\n      background-color: inherit;\r\n      top: 0;\r\n      left:0;\r\n      bottom: 0;\r\n      will-change: left, right;\r\n      // Custom bezier\r\n      animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.840, 0.440, 1.000) infinite;\r\n      animation-delay: 1.15s;\r\n    }\r\n  }\r\n}\r\n@keyframes indeterminate {\r\n    0% {\r\n      left: -35%;\r\n      right:100%;\r\n    }\r\n    60% {\r\n      left: 100%;\r\n      right: -90%;\r\n    }\r\n    100% {\r\n      left: 100%;\r\n      right: -90%;\r\n    }\r\n}\r\n\r\n@keyframes indeterminate-short {\r\n    0% {\r\n      left: -200%;\r\n      right: 100%;\r\n    }\r\n    60% {\r\n      left: 107%;\r\n      right: -8%;\r\n    }\r\n    100% {\r\n      left: 107%;\r\n      right: -8%;\r\n    }\r\n}\r\n\r\n\r\n/*******************\r\n  Utility Classes\r\n*******************/\r\n\r\n.hide {\r\n  display: none !important;\r\n}\r\n\r\n// Text Align\r\n.left-align {\r\n  text-align: left;\r\n}\r\n.right-align {\r\n  text-align: right\r\n}\r\n.center, .center-align {\r\n  text-align: center;\r\n}\r\n\r\n.left {\r\n  float: left !important;\r\n}\r\n.right {\r\n  float: right !important;\r\n}\r\n\r\n// No Text Select\r\n.no-select {\r\n  user-select: none;\r\n}\r\n\r\n.circle {\r\n  border-radius: 50%;\r\n}\r\n\r\n.center-block {\r\n  display: block;\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n}\r\n\r\n.truncate {\r\n  display: block;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n.no-padding {\r\n  padding: 0 !important;\r\n}\r\n",".container {\r\n  margin: 0 auto;\r\n  max-width: 1280px;\r\n  width: 90%;\r\n}\r\n@media #{$medium-and-up} {\r\n  .container {\r\n    width: 85%;\r\n  }\r\n}\r\n@media #{$large-and-up} {\r\n  .container {\r\n    width: 70%;\r\n  }\r\n}\r\n.container .row {\r\n  margin-left: (-1 * $gutter-width / 2);\r\n  margin-right: (-1 * $gutter-width / 2);\r\n}\r\n\r\n.section {\r\n  padding-top: 1rem;\r\n  padding-bottom: 1rem;\r\n\r\n  &.no-pad {\r\n    padding: 0;\r\n  }\r\n  &.no-pad-bot {\r\n    padding-bottom: 0;\r\n  }\r\n  &.no-pad-top {\r\n    padding-top: 0;\r\n  }\r\n}\r\n\r\n\r\n// Mixins to eliminate code repitition\r\n@mixin reset-offset {\r\n  margin-left: auto;\r\n  left: auto;\r\n  right: auto;\r\n}\r\n@mixin grid-classes($size, $i, $perc) {\r\n  &.offset-#{$size}#{$i} {\r\n    margin-left: $perc;\r\n  }\r\n  &.pull-#{$size}#{$i} {\r\n    right: $perc;\r\n  }\r\n  &.push-#{$size}#{$i} {\r\n    left: $perc;\r\n  }\r\n}\r\n\r\n\r\n.row {\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  margin-bottom: 20px;\r\n\r\n  // Clear floating children\r\n  &:after {\r\n    content: \"\";\r\n    display: table;\r\n    clear: both;\r\n  }\r\n\r\n  .col {\r\n    float: left;\r\n    box-sizing: border-box;\r\n    padding: 0 $gutter-width / 2;\r\n    min-height: 1px;\r\n\r\n    &[class*=\"push-\"],\r\n    &[class*=\"pull-\"] {\r\n      position: relative;\r\n    }\r\n\r\n    $i: 1;\r\n    @while $i <= $num-cols {\r\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n      &.s#{$i} {\r\n        width: $perc;\r\n        @include reset-offset;\r\n      }\r\n      $i: $i + 1;\r\n    }\r\n\r\n    $i: 1;\r\n    @while $i <= $num-cols {\r\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n      @include grid-classes(\"s\", $i, $perc);\r\n      $i: $i + 1;\r\n    }\r\n\r\n    @media #{$medium-and-up} {\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        &.m#{$i} {\r\n          width: $perc;\r\n          @include reset-offset;\r\n        }\r\n        $i: $i + 1\r\n      }\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        @include grid-classes(\"m\", $i, $perc);\r\n        $i: $i + 1;\r\n      }\r\n    }\r\n\r\n    @media #{$large-and-up} {\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        &.l#{$i} {\r\n          width: $perc;\r\n          @include reset-offset;\r\n        }\r\n        $i: $i + 1;\r\n      }\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        @include grid-classes(\"l\", $i, $perc);\r\n        $i: $i + 1;\r\n      }\r\n    }\r\n\r\n    @media #{$extra-large-and-up} {\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        &.xl#{$i} {\r\n          width: $perc;\r\n          @include reset-offset;\r\n        }\r\n        $i: $i + 1;\r\n      }\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        @include grid-classes(\"xl\", $i, $perc);\r\n        $i: $i + 1;\r\n      }\r\n    }\r\n  }\r\n}\r\n","nav {\r\n  &.nav-extended {\r\n    height: auto;\r\n\r\n    .nav-wrapper {\r\n      min-height: $navbar-height-mobile;\r\n      height: auto;\r\n    }\r\n\r\n    .nav-content {\r\n      position: relative;\r\n      line-height: normal;\r\n    }\r\n  }\r\n\r\n  color: $navbar-font-color;\r\n  @extend .z-depth-1;\r\n  background-color: $primary-color;\r\n  width: 100%;\r\n  height: $navbar-height-mobile;\r\n  line-height: $navbar-line-height-mobile;\r\n\r\n  a { color: $navbar-font-color; }\r\n\r\n  i,\r\n  [class^=\"mdi-\"], [class*=\"mdi-\"],\r\n  i.material-icons {\r\n    display: block;\r\n    font-size: 24px;\r\n    height: $navbar-height-mobile;\r\n    line-height: $navbar-line-height-mobile;\r\n  }\r\n\r\n  .nav-wrapper {\r\n    position: relative;\r\n    height: 100%;\r\n  }\r\n\r\n  @media #{$large-and-up} {\r\n    a.button-collapse { display: none; }\r\n  }\r\n\r\n\r\n  // Collapse button\r\n  .button-collapse {\r\n    float: left;\r\n    position: relative;\r\n    z-index: 1;\r\n    height: $navbar-height-mobile;\r\n    margin: 0 18px;\r\n\r\n    i {\r\n      height: $navbar-height-mobile;\r\n      line-height: $navbar-line-height-mobile;\r\n    }\r\n  }\r\n\r\n\r\n  // Logo\r\n  .brand-logo {\r\n    position: absolute;\r\n    color: $navbar-font-color;\r\n    display: inline-block;\r\n    font-size: $navbar-brand-font-size;\r\n    padding: 0;\r\n\r\n    &.center {\r\n      left: 50%;\r\n      transform: translateX(-50%);\r\n    }\r\n\r\n    @media #{$medium-and-down} {\r\n      left: 50%;\r\n      transform: translateX(-50%);\r\n\r\n      &.left, &.right {\r\n        padding: 0;\r\n        transform: none;\r\n      }\r\n\r\n      &.left { left: 0.5rem; }\r\n      &.right {\r\n        right: 0.5rem;\r\n        left: auto;\r\n      }\r\n    }\r\n\r\n    &.right {\r\n      right: 0.5rem;\r\n      padding: 0;\r\n    }\r\n\r\n    i,\r\n    [class^=\"mdi-\"], [class*=\"mdi-\"],\r\n    i.material-