\n  @extend .btn;\r\n  height: $button-large-height;\r\n  line-height: $button-large-height;\r\n\r\n  i {\r\n    font-size: $button-large-icon-font-size;\r\n  }\r\n}\r\n\r\n// Block button\r\n.btn-block {\r\n  display: block;\r\n}\r\n",".dropdown-content {\r\n  @extend .z-depth-1;\r\n  background-color: $dropdown-bg-color;\r\n  margin: 0;\r\n  display: none;\r\n  min-width: 100px;\r\n  max-height: 650px;\r\n  overflow-y: auto;\r\n  opacity: 0;\r\n  position: absolute;\r\n  z-index: 999;\r\n  will-change: width, height;\r\n\r\n  li {\r\n    clear: both;\r\n    color: $off-black;\r\n    cursor: pointer;\r\n    min-height: $dropdown-item-height;\r\n    line-height: 1.5rem;\r\n    width: 100%;\r\n    text-align: left;\r\n    text-transform: none;\r\n\r\n    &:hover, &.active, &.selected {\r\n      background-color: $dropdown-hover-bg-color;\r\n    }\r\n\r\n    &.active.selected {\r\n      background-color: darken($dropdown-hover-bg-color, 5%);\r\n    }\r\n\r\n    &.divider {\r\n      min-height: 0;\r\n      height: 1px;\r\n    }\r\n\r\n    & > a, & > span {\r\n      font-size: 16px;\r\n      color: $dropdown-color;\r\n      display: block;\r\n      line-height: 22px;\r\n      padding: (($dropdown-item-height - 22) / 2) 16px;\r\n    }\r\n\r\n    & > span > label {\r\n      top: 1px;\r\n      left: 0;\r\n      height: 18px;\r\n    }\r\n\r\n    // Icon alignment override\r\n    & > a > i {\r\n      height: inherit;\r\n      line-height: inherit;\r\n      float: left;\r\n      margin: 0 24px 0 0;\r\n      width: 24px;\r\n    }\r\n  }\r\n}\r\n\r\n// Input field specificity bugfix\r\n.input-field.col .dropdown-content [type=\"checkbox\"] + label {\r\n  top: 1px;\r\n  left: 0;\r\n  height: 18px;\r\n}\r\n\r\n",".collapsible {\r\n  border-top: 1px solid $collapsible-border-color;\r\n  border-right: 1px solid $collapsible-border-color;\r\n  border-left: 1px solid $collapsible-border-color;\r\n  margin: $element-top-margin 0 $element-bottom-margin 0;\r\n  @extend .z-depth-1;\r\n}\